@import "~@/styles/_variables.scss";






































































































































































































.data-table-wrapper {
  display: block;
  width: 100%;
  overflow-x: auto;
  position: relative;
}
.data-table {
  width: 100%;
}
.data-table__header {
  user-select: none;
  cursor: pointer;
  white-space: nowrap;
  min-width: 1em;
  background-color: $color-grey;
  line-height: 1.6em;
  color: $color-positive;
  font-size: 0.8em;
  padding: 7px 15px;

  &.data-table__header-selectable {
    padding: 7px;
    text-align: center;
  }
}
.data-table__title {
  float: left;
}
.data-table__sort-wrapper {
  display: flex;
  justify-content: flex-end;
  line-height: 1.6em;
}

.data-table__sort--desc,
.data-table__sort--asc {
  display: inline-block;
  color: darken($color-positive, 30%);
  font-size: 1.8em;

  .data-table__sort-wrapper--active & {
    color: $color-positive;
  }
}
.data-table__sort--asc {
  transform: rotate(-90deg);
}
.data-table__sort--desc {
  transform: rotate(90deg);
}
.data-table__row-wrapper {
  background-color: $color-positive;
}
.data-table__row {
  padding: 10px 15px;
  color: $color-grey;
}
.data-table__row-selectable {
  text-align: center;
  padding: 7px;
}
.data-table__action-checkbox {
  cursor: pointer;
}
