@import "~@/styles/_variables.scss";








































.summary {
  margin: 0;
  background-color: $color-positive;
  padding: 2em 0;
  display: inline-flex;
  width: auto;
  box-shadow: 2px 2px 2px 0 rgba($color-negative, 0.05);
  margin-bottom: 1.75em;
}
.summary__item {
  padding: 0 1em;
  font-size: 2em;
  color: $color-grey;
}
.summary__item-description {
  font-size: 0.5em;
  color: $color-muted;
  display: block;
  line-height: 150%;
}
